<template>
  <div class="heartype_wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <div class="btn">
        <Button type="primary" @click="handleHear(true)">批量转书面审理</Button>
      </div>
       <Table
          @on-select="selectOne"
          @on-select-cancel="selectCancel"
          @on-select-all="selectAll"
          @on-select-all-cancel="selectAllCancel"
          :columns="columns" 
          :data="tableData">
        <template slot-scope="{ row }" slot="action">
          <div>
             <a href="javascript:;" style="margin-right:20px" @click="handleHear(false,row)" title="书面审理">
               <img width="26" height="26" src="../../assets/images/shumian.png" alt="">
               </a>
          <a href="javascript:;" style="margin-right:20px"  @click="updateTime(1,row)" title="指定时间">
            <img width="26" height="26" src="../../assets/images/time.png" alt="">
            </a>
          <a href="javascript:;" @click="updateTime(2,row)" title="重新制定开庭时间">
           <img width="26" height="26" src="../../assets/images/repateTime.png" alt="">
            </a>
          </div>
        </template>
      </Table>
      <Page style="margin-top:20px" 
       :current="currentPage" 
       :total="total"
       :page-size="pageSize"
       :page-size-opts="pageOpts" 
       @on-change="changeCurrentPage"
       @on-page-size-change="changeSize"
       show-sizer show-elevator show-total 
      />
    </div>
 
    <!-- 日期 -->
     <Modal v-model="isShowDate" :footer-hide="true" title="提示">
        <div>
            <Form ref="formValidate" :model="form" :rules="ruleValidate" :label-width="110">
                <FormItem :label="isRest?'请重置开庭时间':'请选择开庭时间'" prop="date">
                <DatePicker v-model="form.date"  type="datetime" placeholder="请选择日期" format="yyyy-MM-dd:HH:mm:ss"></DatePicker>
                </FormItem>
            </Form>
            
            <div style="border-top:1px solid #e8eaec;padding:12px 18px 12px 18px;">
                <Button style="margin-right:20px;" @click="isShowDate=false">取消</Button>
                <Button type="primary" @click="selectDate">确定</Button>
            </div>
        </div>
     </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
import { mapActions } from "vuex";
import { listMixin } from "./mixin";
import { dateFtt } from "../../libs/tools.js";
let columns = [
  {
    type: "selection",
    width: 100,
    align: "center"
  },
  {
    title: "案号",
    key: "code",
    align: "center",
    width: 200
  },
  {
    title: "案件类型",
    key: "caseTypeName",
    align: "center"
  },
  {
    title: "申请人",
    key: "applicantName",
    align: "center"
  },
  {
    title: "申请人仲裁员",
    key: "applicantArbName",
    align: "center"
  },
  {
    title: "被申请人",
    key: "respondentName",
    align: "center"
  },
  {
    title: "被申请人仲裁员",
    key: "respondentArbName",
    align: "center"
  },
  {
    title: "确认仲裁员",
    key: "finalArbName",
    align: "center"
  },
  {
    title: "开庭时间",
    key: "beginTime",
    align: "center"
  },
  {
    title: "操作",
    slot: "action",
    width: 200,
    align: "center"
  }
];
export default {
  components: {
    listSearch
  },
  mixins: [listMixin],
  data() {
    return {
      searchData: {
        number: "",
        person: "",
        backPerson: "",
        name: "",
        type: ""
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "person",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backPerson",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "name",
          label: "注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        },
        {
          type: "select",
          value: "type",
          label: "案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        }
      ],
      sureDialog: false,
      isShowDate: false,
      columns: columns,
      hearType: false, // 审理方式 单个 批量
      form: { date: "" },
      ruleValidate: {
        date: [
          {
            required: true,
            type: "date",
            message: "日期不能为空",
            trigger: "blur"
          }
        ]
      },
      isRest: false, // false重置时间 true创建时间
      rowId: ""
    };
  },
  methods: {
    ...mapActions({
      getRequsetList: "createCase/getRequsetList",
      sureWrittenHear: "organizePeriod/sureWrittenHear",
      setCourtTime: "organizePeriod/setCourtTime"
    }),
    //   value true 批量审理 false单个审理
    handleHear(value, row) {
      console.log(row);

      if (value && this.selectedData.length === 0) {
        this.$Message.warning("请至少选择一条案件信息");
        return;
      }
      this.$Modal.confirm({
        title: "同意",
        content: `<p>确定${value ? "批量" : ""}转书面审理？</p>`,
        onOk: async () => {
          const send = await this.sureWrittenHear({
            actionIdentifer: "GROUP_SELECT_WRITTEN",
            caseId: JSON.stringify(new Array(row.id))
          });

          if (send.data.flag) {
            this.$Message.success("操作成功");
            this.getList();
          } else {
            this.$Message.error(send.data.message);
          }
        },
        onCancel: () => {}
      });
    },
    updateTime(value, row) {
      console.log(row);
      this.isShowDate = true;
      this.rowId = row.id;
      if (value == 1) {
        this.isRest = false;
      } else {
        this.isRest = true;
      }
    },
    restTime(row) {
      console.log(row);
      this.isRest = false;
      this.isShowDate = true;
    },
    goSearch() {},
    handleChange(selection) {
      console.log(selection, "handleChange");
      this.selectedData = selection;
    },
    success() {
      if (this.hearType) {
        // 批量
        this.$Message.success("批量 =操作成功");
      } else {
        // 单个
        this.$Message.error("单个===系统出错，请稍后重试");
      }
    },
    async selectDate() {
      this.$refs["formValidate"].validate(async valid => {
        console.log(this.form.date, this.isRest, valid);
        if (valid) {
          // this.$Message.success("Success!");caseId: )
          let params = {
            actionIdentifer: "GROUP_SELECT_BEGINTIME",
            caseId: JSON.stringify(new Array(this.rowId)), //案件id 批量用','隔开示例"1,2,3,4"
            beginTime: dateFtt("yyyy-MM-dd hh:mm:ss", this.form.date) // 开庭时间
          };
          console.log(params, "-");

          if (!this.isRest) { // 设置
            const result = await this.setCourtTime(params);
            if (result.data.flag) {
              this.$Message.success("操作成功");
              this.getList();
            } else {
              this.$Message.error(result.data.message);
            }
          }
          // this.isShowDate=false
        } else {
          // this.$Message.error("Fail!");
        }
      });
    },
    async getList() {
      let params = {
        menuIdentifer: "GROUP_STYLE_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter = {
        //筛选条件
        applicantName: "", //申请人关键字
        respondentName: "", //被申请人关键字
        userId: "", //注册名称的用户id
        caseTypeId: "", //案件类型id
        createTimeBegin: "", //提交的开始时间
        createTimeEnd: "" //提交的结束时间
      };

      params = { ...params, filter: JSON.stringify({}) };
      // console.log("参数", params);
      const res = await this.getRequsetList(params);
      if (res.data.flag) {
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
        this.selectedData = []; // 重置
      }
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}
/deep/.ivu-modal-footer {
  text-align: center;
}
.heartype_wrap {
  .table_box {
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    .btn {
      text-align: right;
      margin-bottom: 20px;
    }
  }
}
</style>
